  <div
    ref="addEditModal"
    class="modal fade"
    data-bs-keyboard="false"
    data-bs-backdrop="static"
    tabindex="-1"
    aria-labelledby="productModalLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-dialog-centered modal-xl">
      <div class="modal-content">
        <div class="modal-header">
          <h3 class="modal-title">
            <span v-if="isNew">新增產品</span>
            <span v-else>編輯產品</span>
          </h3>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-sm-4">
              <div class="form-group mb-4">
                <label for="image"
                  >主要圖片
                  <input
                    type="text"
                    class="form-control"
                    placeholder="請輸入圖片連結"
                    v-model="tempProduct.imageUrl"
                  />
                </label>
                <img class="img-fluid" :src="tempProduct.imageUrl" alt="" />
              </div>
              <div class="mb-1">多圖新增</div>
              <div v-if="Array.isArray(tempProduct.imagesUrl)">
                <div class="mb-1" v-for="(image, key) in tempProduct.imagesUrl" :key="key">
                  <div class="form-group">
                    <label for="imageUrl"
                      >圖片網址
                      <input
                        type="text"
                        class="form-control"
                        placeholder="請輸入圖片連結"
                        v-model="tempProduct.imagesUrl[key]"
                      />
                    </label>
                  </div>
                  <img class="img-fluid" :src="image" alt="" />
                </div>
                <div
                  v-if="
                    !tempProduct.imagesUrl.length ||
                    tempProduct.imagesUrl[tempProduct.imagesUrl.length - 1]
                  "
                >
                  <button
                    class="btn btn-outline-primary btn-sm d-block w-100"
                    @click="tempProduct.imagesUrl.push('')"
                  >
                    新增圖片
                  </button>
                </div>
                <div v-else>
                  <button
                    class="btn btn-outline-danger btn-sm d-block w-100"
                    @click="tempProduct.imagesUrl.pop()"
                  >
                    刪除圖片
                  </button>
                </div>
              </div>
              <div v-else>
                <button
                  class="btn btn-outline-secondary btn-sm d-block w-100"
                  @click="createImages"
                >
                  新增陣列圖片
                </button>
              </div>
            </div>
            <div class="col-sm-8">
              <div class="form-group mb-3">
                <label for="title"
                  >甜點名稱
                  <input
                    id="title"
                    v-model="tempProduct.title"
                    type="text"
                    class="form-control"
                    placeholder="請輸入名稱"
                  />
                </label>
              </div>
              <div class="row mb-3">
                <div class="form-group mb-3 col-4">
                  <label for="title"
                    >甜點售價
                    <input
                      id="title"
                      v-model="tempProduct.price"
                      type="number"
                      class="form-control"
                      placeholder="請輸入名稱"
                    />
                  </label>
                </div>
                <div class="form-group mb-3 col-4">
                  <label for="title"
                    >原始售價
                    <input
                      id="title"
                      v-model="tempProduct.origin_price"
                      type="number"
                      class="form-control"
                      placeholder="請輸入名稱"
                    />
                  </label>
                </div>
              </div>
              <div class="row">
                <div class="form-group col-4">
                  <label for="category"
                    >甜點分類
                    <select class="form-control" v-model="tempProduct.category">
                      <option disabled selected>請選擇分類</option>
                      <option>pie</option>
                      <option>cake</option>
                      <option>cookie</option>
                    </select>
                  </label>
                </div>
                <div class="form-group col-4">
                  <div class="mb-1">多尺寸新增</div>
                  <div v-if="Array.isArray(tempProduct.size)">
                    <div class="mb-1">
                      <div class="form-group">
                        <label for="imageUrl"
                          >尺寸or數量
                          <template v-for="(item, key) in tempProduct.size" :key="key">
                            <input
                              type="number"
                              class="form-control"
                              v-model="tempProduct.size[key]"
                            />
                          </template>
                        </label>
                      </div>
                    </div>
                    <div
                      v-if="
                        !tempProduct.size.length || tempProduct.size[tempProduct.size.length - 1]
                      "
                    >
                      <button
                        class="btn btn-outline-primary btn-sm d-block w-100"
                        @click="tempProduct.size.push('')"
                      >
                        新增尺寸
                      </button>
                    </div>
                    <div v-else>
                      <button
                        class="btn btn-outline-danger btn-sm d-block w-100"
                        @click="tempProduct.size.pop()"
                      >
                        刪除尺寸
                      </button>
                    </div>
                  </div>
                  <div v-else>
                    <button
                      class="btn btn-outline-secondary btn-sm d-block w-100"
                      @click="createSize"
                    >
                      新增陣列尺寸
                    </button>
                  </div>
                </div>
                <div class="form-group col-4">
                  <label for="unit"
                    >甜點單位(" g 個)
                    <input
                      id="unit"
                      v-model="tempProduct.unit"
                      type="text"
                      class="form-control"
                      placeholder="請輸甜點單位"
                    />
                  </label>
                </div>
              </div>
              <hr />
              <div class="form-group mb-3">
                甜點描述
                <textarea
                  v-model="tempProduct.description"
                  type="text"
                  class="form-control"
                  placeholder="甜點描述"
                  style="height: 150px"
                >
                </textarea>
              </div>
              <div class="form-group mb-3">
                甜點成分
                <textarea
                  v-model="tempProduct.content"
                  type="text"
                  class="form-control"
                  placeholder="甜點成分"
                  style="height: 150px"
                >
                </textarea>
              </div>
              <div class="form-group">
                <div class="form-check">
                  <label class="form-check-label" for="isPublic">
                    <span class="d-none">是否啟用</span>
                    <input
                      id="isPublic"
                      v-model="tempProduct.is_enabled"
                      class="form-check-input"
                      type="checkbox"
                      :true-value="1"
                      :false-value="0"
                    />
                  </label>
                  是否啟用
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <div class="text-center" :class="{ 'd-none': loading == false }">
            <div class="spinner-border" role="status">
              <span class="visually-hidden">Loading...</span>
            </div>
          </div>
          <button
            type="button"
            class="btn btn-outline-danger me-5"
            data-bs-dismiss="modal"
            :class="{ 'd-none': loading == true }"
          >
            取消
          </button>
          <button
            type="button"
            class="btn btn-secondary"
            @click="updateProduct(tempProduct)"
            :class="{ 'd-none': loading == true }"
          >
            確認
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Modal from 'bootstrap/js/dist/modal';

export default {
  props: ['product', 'isNew'],
  data() {
    return {
      tempProduct: {},
      status: '',
      modal: '',
      loading: false,
      tempTime: '',
    };
  },
  watch: {
    product() {
      this.tempProduct = this.product;
    },
    loading() {
      this.status = this.loading;
    },
  },
  mounted() {
    this.modal = new Modal(this.$refs.addEditModal);
  },
  methods: {
    createImages() {
      this.tempProduct.imagesUrl = [''];
    },
    createSize() {
      this.tempProduct.size = [''];
    },
    updateProduct(temp) {
      this.loading = true;
      let api = '/api/admin/product';
      let method = 'post';
      if (temp.id) {
        api = `/api/${process.env.VUE_APP_PATH}/admin/product/${temp.id}`;
        method = 'put';
      }
      this.$http[method](api, { data: temp })
        .then((res) => {
          if (res.data.success) {
            this.$emit('render');
            this.loading = false;
            this.hideModal();
          } else {
            // eslint-disable-next-line no-alert
            alert(res.data.message);
            this.loading = false;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    openModal() {
      this.modal.show();
    },
    hideModal() {
      this.modal.hide();
    },
  },
};
</script>
