<!-- eslint-disable vuejs-accessibility/click-events-have-key-events -->
<template>
<section class="mb-3">
      <div class="d-flex align-items-center justify-content-between">
        <h4>文章列表</h4>
        <button
          type="button"
          class="btn btn-outline-dark btn-lg rounded-3"
          @click="openModal(true)"
        >
          <i class="fa-solid fa-plus me-2"></i>新增甜點
        </button>
        <label for="inputState" class="form-label"
          ><p class="d-none">State</p>
          <select id="inputState" class="form-select rounded-3 p-2 px-3">
            <option selected>新->舊...</option>
            <option>按類別</option>
          </select>
        </label>
      </div>
    </section>
  <section class="row">
    <table class="table">
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col">分類</th>
          <th scope="col">品名</th>
          <th scope="col">價格</th>
          <th scope="col">是否啟用</th>
          <th scope="col">修改</th>
          <th scope="col">刪除</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, id, i) in products" :key="item.id">
          <th scope="row">{{ i + 1 }}</th>
          <td>{{ item.category }}</td>
          <td>{{ item.title }}</td>
          <td>{{ item.price }}</td>
          <td>
            <i v-if="item.is_enabled" class="bi bi-check-square-fill text-success-emphasis"></i>
            <i v-else class="bi bi-dash-square-fill text-danger-emphasis"></i>
          </td>
          <td><i class="bi bi-pencil-square btn" @click="openModal(false, item)"></i></td>
          <td>
            <i class="bi bi-x-circle btn text-warning-emphasis" @click="checkModel(item)"></i>
          </td>
        </tr>
      </tbody>
    </table>
  </section>
  <!-- 新增＆編輯 -->
  <AddEdit ref="addedit" :product="product" @render="getProducts" />
  <!-- 確認model -->
  <CheckDelete ref="check" :product="product" @delete="deleteProduct" />
</template>

<script>
import AddEdit from '@/components/dash/AddEditFirebase.vue';
import CheckDelete from '@/components/dash/CheckModel.vue';

export default {
  data() {
    return {
      products: [],
      product: {},
      checkSuccess: false,
      modle: '',
    };
  },
  components: {
    CheckDelete,
    AddEdit,
  },
  methods: {
    checkLogin() {
      const token = document.cookie.replace(
        /(?:(?:^|.*;\s*)LuluBakeToken\s*=\s*([^;]*).*$)|^.*$/,
        '$1',
      );
      if (token) {
        // Axios 預設值
        this.$http.defaults.headers.common.Authorization = `${token}`;
        const api = `${process.env.VUE_APP_API2}/api/user/check`;
        this.$http.post(api, { api_token: this.token }).then((response) => {
          if (response.data.success) {
            this.checkSuccess = true;
            this.getProducts();
          } else {
            // eslint-disable-next-line no-alert
            alert(response.data.message);
            this.$router.push('/login');
          }
        });
      } else {
        // eslint-disable-next-line no-alert
        alert('您尚未登入。');
        this.$router.push('/login');
      }
    },
    getProducts() {
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/products/all`;
      this.$http.get(api).then((res) => {
        this.products = res.data.products;
      });
    },
    checkModel(item, status) {
      this.product = item;
      if (status) {
        this.$refs.addedit.openModal();
      } else {
        this.$refs.check.openModal();
      }
    },
    // open add&edit Modal
    openModal(isNew, item) {
      this.isNew = isNew;
      if (this.isNew) {
        this.product = {
          imagesUrl: [],
          size: [],
        };
      } else {
        this.product = { ...item };
      }
      this.$refs.addedit.openModal();
    },
    deleteProduct(id) {
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/product/${id}`;
      this.$http.delete(api).then((res) => {
        console.log(res);
        this.getProducts();
      });
    },
  },
  mounted() {
    this.checkLogin();
  },
};
</script>
